ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  width: 200px;
  background-color: #212327;
  height: 100%; /* Full height */
  position: fixed; /* Make it stick, even on scroll */
  overflow: auto; /* Enable scrolling if the sidenav has too much content */
}
li a {
  display: block;
  color: #fff;
  padding: 8px 16px;
  text-decoration: none;
}

/* Change the link color on hover */
li a:hover {
  background-color: #023A6B;
  text-decoration: none;
  border-left: #fff 4px solid;
  color: white;
}
.active {
  background-color: #023A6B;
  text-decoration: none;
  border-left: #fff 4px solid;
  color: white;
}
.navheader {
  color: #fff;
  padding: 20px 10px;
}

.navigation {
  width: 125px;
}


.loadingContainer {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    text-align: center;
}
.loadingContent {
    position:absolute;
    top:50%;
    left:50%;
}

.loadingContent h1 {
  color: #023A6B;
}


.flip-card {
  background-color: transparent;
  width: 20%;
  height: 250px;
  perspective: 1000px; /* Remove this if you don't want the 3D effect */
  padding: 15px;
}

/* This container is needed to position the front and back side */
.flip-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.8s;
  transform-style: preserve-3d;
}

/* Do an horizontal flip when you move the mouse over the flip box container */
.flip-card:hover .flip-card-inner {
  transform: rotateY(180deg);
}

/* Position the front and back side */
.flip-card-front, .flip-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
}

/* Style the front side (fallback if image is missing) */
.flip-card-front {
  background-color: #212327;
  color: white;
}

/* Style the back side */
.flip-card-back {
  background-color: #023A6B;
  color: white;
  transform: rotateY(180deg);
}
.flip-card-back a {
  color: #fff;
  text-decoration: none;
  font-size: 3rem;
  padding: 0;
  margin: 0;
}

.flip-card i {
  padding: 0;
  margin: 0;
}

.flip-card-back a:hover {
  color: #212327;
}
.flip-card-back p {
  padding: 0 15px;
  font-size: 0.8rem;
}

.container {
  width: 80%;
  margin: auto;
}
.quote {
  color: #212327;
  border-left: #023A6B 5px solid;
  padding: 15px;
  font-size: 1.7rem;
}
.sectionDivider {
  border: 2px solid #023A6B;
  border-radius: 5px;
}
.text {
  font-size: 1.2rem;
  line-height: 2rem;
}
.focus {
  font-size: 1.5rem;
  color: #023A6B;
  font-weight: bold;
}

.skillContainer {
  text-align: center;
}

.skillContainer img {
  width: 75%;
}

.card {
  border: #023A6B 1px solid;
  padding: 15px;
  margin: 20px 0;
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}

.card hr {
  border: #023A6B 1px solid;
  opacity: 0.5;
}
.pageHeader {
  width: 100%;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 10%;
  background-color: #023A6B;
  color: #fff;
}
.viewContainer {
  padding-left: 200px;
}
.typewriter {
  position: absolute;
  top: 50%;
}
.typewriter h1 {
  color: #023A6B; 
  font-size: 50px;
  white-space: nowrap;
  overflow: hidden;
  width: 30em;
  animation: type 4s steps(60, end);  
}

.typwriter h1:nth-child(2){
  animation: type2 8s steps(60, end);
}

.typewriter h1 a{
  color: lime;
  text-decoration: none;
}

.typewriter span{
  animation: blink 1s infinite;
}

@keyframes type{ 
  from { width: 0; } 
} 

@keyframes type2{
  0%{width: 0;}
  50%{width: 0;}
  100%{ width: 100; } 
} 

@keyframes blink{
  to{opacity: .0;}
}

